@viewport {
  width: device-width;
  zoom: 1;
}

.App {
  position: relative;
  text-align: center;
  min-height: 100vh;
  min-width: 100vw;
  width: 100%;
  height: 100%;
  margin: 0px;
  /* background: rgb(242, 174, 167); */
  background: linear-gradient(
    346deg,
    rgba(242, 174, 167, 1) 8%,
    rgba(215, 75, 82, 1) 35%,
    rgba(90, 35, 228, 1) 100%
  );
}

footer {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 6em;
  background: linear-gradient(
    346deg,
    rgba(242, 174, 167, 1) 8%,
    rgba(215, 75, 82, 1) 35%
  );
  color: rgba(240, 231, 231, 0.952);
  padding: 2em 0em 2em 0em;
}

/*Start*/

.loading {
  padding: 1em;
  margin: 0em;
}

#gameName {
  margin: 0;
  padding: 1em 0em 1em 0em;
  color: white;
}

.start {
  display: flex;
  flex-direction: column;
  padding: 5em;
  background-color: white;
  margin: 0em 3em 0em 3em;
  border-radius: 10px;
  box-shadow: inset 0.5em 0.5em 1em lightgrey,
    12px 12px 2px 1px rgb(34 34 142 / 20%);
  border: salmon double 0.5em;
}

.playerInput {
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  align-self: center;
  margin: 1em;
}

.playerInput > input {
  margin: 0.5em 0em 0.5em 0em;
}

/* Question */

.question {
  display: flex;
  flex-direction: column;
  background-color: rgba(216, 112, 147, 0.144);
}

.topBanner {
  display: flex;
  justify-content: space-between;
  padding: 0em 2em 0em 2em;
  background-color: black;
  padding: 1em;
  color: white;
}

.playerInput .answer {
  padding: 0.5em;
}

/* Results */

.results {
  min-width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.resultBtns {
  margin: 1em;
}

.resultBtns button {
  margin: 1em;
  padding: 0.5em;
}

.answerSheet {
  display: flex;
  flex-direction: column;
  align-items: center;
  /* justify-content: center; */
  width: 35%;
}

table {
  border: black solid;
  object-fit: fill;
}

/* .row {
  border: black solid;
} */

.incorrect_row {
  background-color: rgba(255, 0, 0, 0.507);
}

.correct_row {
  background-color: rgba(111, 233, 111, 0.863);
}

.row_left,
.row_right {
  padding: 0.5em;
  border: black solid;
}
